"use client";

import { useState, useEffect, useRef } from "react";
import { Card } from "@/components/ui/card";
import { Input } from "@/components/ui/input";
import { Button } from "@/components/ui/button";
import { ScrollArea } from "@/components/ui/scroll-area";
import { Avatar, AvatarFallback, AvatarImage } from "@/components/ui/avatar";

// Types
type Message = {
  id: string;
  senderId: string;
  receiverId: string;
  content: string;
  timestamp: Date;
};

type Match = {
  id: string;
  name: string;
  photoUrl: string;
  lastMessage?: string;
  lastMessageTime?: Date;
  unreadCount: number;
};

// Mock data
const mockMatches: Match[] = [
  {
    id: "1",
    name: "Aditya Sharma",
    photoUrl: "https://images.pexels.com/photos/2379004/pexels-photo-2379004.jpeg",
    lastMessage: "Hello, how are you?",
    lastMessageTime: new Date(),
    unreadCount: 2,
  },
  {
    id: "2",
    name: "Priya Mishra",
    photoUrl: "https://images.pexels.com/photos/3760583/pexels-photo-3760583.jpeg",
    lastMessage: "Nice to meet you!",
    lastMessageTime: new Date(),
    unreadCount: 0,
  },
  // Add more mock matches as needed
];

const mockMessages: Message[] = [
  {
    id: "1",
    senderId: "user1",
    receiverId: "1",
    content: "Hello, how are you?",
    timestamp: new Date(Date.now() - 1000 * 60 * 5), // 5 minutes ago
  },
  {
    id: "2",
    senderId: "1",
    receiverId: "user1",
    content: "Hi! I'm good, thank you. How about you?",
    timestamp: new Date(Date.now() - 1000 * 60 * 4), // 4 minutes ago
  },
  // Add more mock messages as needed
];

export default function ChatPage() {
  const [selectedMatch, setSelectedMatch] = useState<Match | null>(null);
  const [matches, setMatches] = useState<Match[]>(mockMatches);
  const [messages, setMessages] = useState<Message[]>(mockMessages);
  const [newMessage, setNewMessage] = useState("");
  const messagesEndRef = useRef<HTMLDivElement>(null);

  const scrollToBottom = () => {
    messagesEndRef.current?.scrollIntoView({ behavior: "smooth" });
  };

  useEffect(() => {
    scrollToBottom();
  }, [messages]);

  const handleSendMessage = (e: React.FormEvent) => {
    e.preventDefault();
    if (!newMessage.trim() || !selectedMatch) return;

    const message: Message = {
      id: Date.now().toString(),
      senderId: "user1",
      receiverId: selectedMatch.id,
      content: newMessage,
      timestamp: new Date(),
    };

    setMessages([...messages, message]);
    setNewMessage("");
  };

  const formatTime = (date: Date) => {
    return new Intl.DateTimeFormat("en-US", {
      hour: "numeric",
      minute: "numeric",
      hour12: true,
    }).format(date);
  };

  return (
    <div className="min-h-screen bg-gradient-to-b from-white to-gray-50 py-12">
      <div className="container mx-auto px-4">
        <div className="max-w-6xl mx-auto">
          <h1 className="text-3xl font-bold text-[#800000] mb-8">Messages</h1>

          <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
            {/* Matches List */}
            <Card className="p-4 md:col-span-1">
              <h2 className="text-xl font-semibold mb-4">Your Matches</h2>
              <ScrollArea className="h-[600px]">
                <div className="space-y-2">
                  {matches.map((match) => (
                    <div
                      key={match.id}
                      className={`p-3 rounded-lg cursor-pointer transition-colors ${
                        selectedMatch?.id === match.id
                          ? "bg-[#800000] text-white"
                          : "hover:bg-gray-100"
                      }`}
                      onClick={() => setSelectedMatch(match)}
                    >
                      <div className="flex items-center space-x-3">
                        <Avatar>
                          <AvatarImage src={match.photoUrl} alt={match.name} />
                          <AvatarFallback>
                            {match.name.charAt(0)}
                          </AvatarFallback>
                        </Avatar>
                        <div className="flex-1">
                          <h3 className="font-semibold">{match.name}</h3>
                          {match.lastMessage && (
                            <p className={`text-sm ${
                              selectedMatch?.id === match.id
                                ? "text-gray-200"
                                : "text-gray-500"
                            }`}>
                              {match.lastMessage}
                            </p>
                          )}
                        </div>
                        {match.unreadCount > 0 && (
                          <span className="bg-[#FFD700] text-[#800000] text-xs font-bold px-2 py-1 rounded-full">
                            {match.unreadCount}
                          </span>
                        )}
                      </div>
                    </div>
                  ))}
                </div>
              </ScrollArea>
            </Card>

            {/* Chat Area */}
            <Card className="p-4 md:col-span-2">
              {selectedMatch ? (
                <div className="h-[600px] flex flex-col">
                  {/* Chat Header */}
                  <div className="flex items-center space-x-3 p-3 border-b">
                    <Avatar>
                      <AvatarImage
                        src={selectedMatch.photoUrl}
                        alt={selectedMatch.name}
                      />
                      <AvatarFallback>
                        {selectedMatch.name.charAt(0)}
                      </AvatarFallback>
                    </Avatar>
                    <div>
                      <h3 className="font-semibold">{selectedMatch.name}</h3>
                      <p className="text-sm text-gray-500">Online</p>
                    </div>
                  </div>

                  {/* Messages */}
                  <ScrollArea className="flex-1 p-4">
                    <div className="space-y-4">
                      {messages
                        .filter(
                          (msg) =>
                            msg.senderId === selectedMatch.id ||
                            msg.receiverId === selectedMatch.id
                        )
                        .map((message) => (
                          <div
                            key={message.id}
                            className={`flex ${
                              message.senderId === "user1"
                                ? "justify-end"
                                : "justify-start"
                            }`}
                          >
                            <div
                              className={`max-w-[70%] p-3 rounded-lg ${
                                message.senderId === "user1"
                                  ? "bg-[#800000] text-white"
                                  : "bg-gray-100"
                              }`}
                            >
                              <p>{message.content}</p>
                              <p
                                className={`text-xs mt-1 ${
                                  message.senderId === "user1"
                                    ? "text-gray-300"
                                    : "text-gray-500"
                                }`}
                              >
                                {formatTime(message.timestamp)}
                              </p>
                            </div>
                          </div>
                        ))}
                      <div ref={messagesEndRef} />
                    </div>
                  </ScrollArea>

                  {/* Message Input */}
                  <form onSubmit={handleSendMessage} className="p-3 border-t">
                    <div className="flex space-x-2">
                      <Input
                        value={newMessage}
                        onChange={(e) => setNewMessage(e.target.value)}
                        placeholder="Type a message..."
                        className="flex-1"
                      />
                      <Button 
                        type="submit"
                        className="bg-[#800000] hover:bg-[#600000]"
                      >
                        Send
                      </Button>
                    </div>
                  </form>
                </div>
              ) : (
                <div className="h-[600px] flex items-center justify-center">
                  <p className="text-gray-500">
                    Select a match to start chatting
                  </p>
                </div>
              )}
            </Card>
          </div>
        </div>
      </div>
    </div>
  );
}
